import { Injectable } from '@angular/core';
import { User } from './user';
import { HttpHeaders, HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { CookieService } from 'ngx-cookie-service';
import { UserInfo } from '../dashboard/dashboard.service';

@Injectable({
  providedIn: 'root'
})
export class LoginService {

  private isUserLoggedIn: boolean;
  private logged_user: number = 0;
  private auth_token: string = '';
  private cookie_expiry_date: Date = new Date();

  constructor(private http: HttpClient, private cookie: CookieService) {
    this.isUserLoggedIn = false;

    // Added the expiry time for the cookies
    this.cookie_expiry_date.setDate(this.cookie_expiry_date.getDate() + 1);
  }

  public getUserLoggedIn(): boolean {
    // return this.isUserLoggedIn;
    if (this.cookie.check('isUserLoggedIn')) {
      if (this.cookie.get('isUserLoggedIn') == 'true') {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  public setUserLoggedIn(response) {
    if (response) {
      this.cookie.set('isUserLoggedIn', 'true', this.cookie_expiry_date);
    } else {
      this.cookie.set('isUserLoggedIn', 'false', this.cookie_expiry_date);
    }
    //this.isUserLoggedIn = response;
  }

  public setLoggedUser(user_id: number) {
    this.cookie.set('logged_user', user_id.toString(), this.cookie_expiry_date);
  }

  public getLoggedUser() {
    if (this.cookie.check('logged_user')) {
      return Number.parseInt(this.cookie.get('logged_user'));
    } else {
      return this.logged_user;
    }
  }

  public getAuthToken() {
    if (this.cookie.check('auth_token')) {
      return this.cookie.get('auth_token');
    } else {
      return this.auth_token;
    }
    //return this.auth_token;
  }

  public setAuthToken(auth_token) {
    this.cookie.set('auth_token', auth_token, this.cookie_expiry_date);
    //this.auth_token = auth_token;
  }

  public loginUser(user: User) {
    // Setting the cookies value.
    this.auth_token = 'Basic ' + btoa(user.user_email + ':' + user.user_password);
    this.setAuthToken(this.auth_token);


    // this.http.get('/api/users/login/'+user.user_email, {headers: headers})
    return this.http.get<UserInfo>('/api/users/login/' + user.user_email)
      .pipe(
        catchError(this.handleError)
      );
  }

  public logoutUser() {

    // this.http.get('/api/users/login/'+user.user_email, {headers: headers})
    return this.http.get('/api/logout')
      .pipe(
        catchError(this.handleError)
      );
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      error);
  }
}
