import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastService } from '../toast.service';
import { PasswordService } from '../forgetpassword/password.service';
import { HttpErrorResponse } from '@angular/common/http';

class ForgetPassword {
  // Password models
  password: string ;
  cpassword: string ;
}

@Component({
  selector: 'app-resetpassword',
  templateUrl: './resetpassword.component.html',
  styleUrls: ['./resetpassword.component.css']
})

export class ResetpasswordComponent implements OnInit {

  // Forget password model
  forget_pass_model = new ForgetPassword();
  token: string;

  constructor(private router: Router, private toast: ToastService, private activeRoute: ActivatedRoute,
    private password_service: PasswordService) { }

  ngOnInit() {
   this.token = this.activeRoute.snapshot.paramMap.get('token');
  }

  resetPass() {
    if (this.forget_pass_model. password === this.forget_pass_model.cpassword) {

      // Code for sending the password reset link
    this.password_service.resetPassword(this.token, this.forget_pass_model.password).subscribe(
      data => {
        this.toast.Success('Success', 'Password has been reseted please login again...');
        this.router.navigateByUrl('/login');
      },
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null) {
        } else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status === 404) {
            this.toast.Error('Invalid Link', 'Password reset link has been expired...');
            this.router.navigateByUrl('/login');
          } else if ( error.status === 504) {
            this.toast.Error('Server Issue', 'Server is not responding!!!');
          }
        }
      });
    } else {
      this.toast.Error('Failure', 'Password are not equal...');
    }
  }
}
