import { Component, OnInit } from '@angular/core';
import { User } from './user';
import { Router } from '@angular/router';
import { LoginService } from './login.service';
import { HttpErrorResponse } from '@angular/common/http';
import { ToastService } from '../toast.service';
import { PasswordService } from '../forgetpassword/password.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  login = {

    buttontext: 'Register',
    buttonlink: '/register'
  };

  // Error messages
  private error_message: String;

  // Initilizing the form data
  model = new User();

  // User email address field for sending.
  public user_email: string;

  constructor(private router: Router, private password_service: PasswordService,
    private login_service: LoginService, private toast: ToastService) { }

  ngOnInit() {
    // If user alresdy logged in then redirect to dashboard page
    if (this.login_service.getUserLoggedIn()) {
      this.router.navigate(['dashboard']);
    }
  }

  onSubmit() {
    console.log('Credentails:', this.model);
    this.login_service.loginUser(this.model).subscribe( // If any data paresent
      data => {
        this.toast.Success('Login', 'User has been logged in Successfully');
        this.login_service.setUserLoggedIn(true);
        this.login_service.setLoggedUser(data.user_id);
        this.router.navigate(['dashboard']);
      },
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null) { }
        else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status == 404 || error.status == 504) {
            this.toast.Error('Server Issue', 'Server is not responding!!!');
          } else if (error.status == 401 || error.status == 403) {
            this.toast.Error('Login Failed', 'Please Enter Valid Email Id/Password');
          }
        }
      });
    // if(this.model.user_email == 'admin' && this.model.user_password == 'admin'){
    //   this.login_service.setUserLoggedIn();
    //   this.router.navigate(['dashboard']);
    // }
  }

  sendResetLink() {
    // Code for sending the password reset link
    this.password_service.sendPasswordLink(this.user_email).subscribe(
      data => {
        this.router.navigateByUrl('/forgetpasswordsuccess');
      },
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null) {
        } else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status === 404 ) {
            this.toast.Error('Invalid Email', 'Specified email is not been registered '
            + 'with any user accout please re-enter your correct email address.');
          } else if (error.status === 504) {
            this.toast.Error('Server Issue', 'Server is not responding!!!');
          } else if (error.status === 401 || error.status === 403) {
            this.toast.Error('Login Failed', 'No user registered with this' + this.user_email + ' address');
          }
        }
      });

  }
}
