import { Component, OnInit, OnChanges } from '@angular/core';
import { CategoryInfo, QuestionpageService, QuestionInfo, questionOptionsInfo } from './questionpage.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastService } from '../toast.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-questionpage',
  templateUrl: './questionpage.component.html',
  styleUrls: ['./questionpage.component.css']
})
export class QuestionpageComponent implements OnInit {

  // Variables for the hold category data
  category: CategoryInfo[];

  // Varible to hold the quesion data
  questions: QuestionInfo[];

  // Assessment id
  assessment_id: number;

  // Category id
  category_id: number;

  // Selected Category
  selected_category: CategoryInfo;

  // Next button
  next: String = 'Next';

  headers: any;

  config: any;

  // Hiding previous button in 1st page
  isPreviousVisible: Boolean = true;

  constructor(private cat_service: QuestionpageService, private router: ActivatedRoute,
    private toast: ToastService, private route: Router) {
    // Get the data form the path variable.
    this.assessment_id = parseInt(this.router.snapshot.paramMap.get('assessment_id'));
    this.category_id = parseInt(this.router.snapshot.paramMap.get('category_id'));
  }

  ngOnInit() {

    // Code for pulling the category form API
    this.cat_service.getCategory().subscribe(
      data => {
        this.category = data;
        this.selected_category = this.category[0];
      },
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null) {
        } else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status === 404 || error.status === 504) {
            this.toast.Error('Server Issue', 'Server is not responding!!!');
          } else if (error.status === 401 || error.status === 403) {
            this.toast.Error('Login Failed', 'Please Enter Valid Email Id/Password');
          }
        }
      });

    // Calling of the question API
    this.getQuestions();
  }


  /**
   * This method is created to get the questions
   */
  getQuestions() {
    // Code for pulling the questions form Questions API
    this.cat_service.getQuestions(this.assessment_id, this.category_id).subscribe(
      data => {
        this.questions = data;
        console.log('Questions', data);

      },
      // If any error present
      (error: HttpErrorResponse) => {
        if (error === null) {
        } else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status === 404 || error.status === 504) {
            this.toast.Error('Server Issue', 'Server is not responding!!!');
          } else if (error.status === 401 || error.status === 403) {
            this.toast.Error('Login Failed', 'Please Enter Valid Email Id/Password');
          }
        }
      }
    );
  }

  /**
   * This method is created to get the questions
   */
  saveQuestions() {
    // Code for pulling the questions form Questions API
    this.cat_service.saveQuestionAnswer(this.assessment_id, this.category_id, this.questions).subscribe(
      data => {
         // checking wheather the last category is answered
        if (this.category.length === this.category_id) {
          // Submitting the assessment
          this.submitAssessment();

        } else {
          // calling the next category question
          const cat: CategoryInfo = this.getCategoryById(this.category_id + 1);
          console.log('Category Info', cat);

          // Redirecting to next category
          this.onCategoryChange(cat);
        }
      },
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null) {
        } else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status === 404 || error.status === 504) {
            this.toast.Error('Server Issue', 'Server is not responding!!!');
          } else if (error.status === 401 || error.status === 403) {
            this.toast.Error('Login Failed', 'Please Enter Valid Email Id/Password');
          }
        }
      }
    );
  }

  /**
   * This method is created to get the questions
   */
  submitAssessment() {
    // Code for pulling the questions form Questions API
    this.cat_service.submitAssessment(this.assessment_id).subscribe(
      resp => {
        console.log('status code', resp.status);
        if (resp.status === 200) {
          this.toast.Success('Submit assessment', 'All questions has been answered successfully...');
          // Redirecting to the result page
          this.route.navigate(['/assessment', this.assessment_id, 'result']);
        } else {
          this.toast.Error('Submit assessment', 'Still some questions are need to answer!!!');
        }
      },
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null) {
        } else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status === 404 || error.status === 504) {
            this.toast.Error('Server Issue', 'Server is not responding!!!');
          } else if (error.status === 401 || error.status === 403) {
            this.toast.Error('Login Failed', 'Please Enter Valid Email Id/Password');
          }
        }
      }
    );
  }

  // On onCategoryChange()
  onCategoryChange(category: CategoryInfo) {
    // Navigating to new page
    this.route.navigate(['/assessment', this.assessment_id, 'category', category.category_id]).then(onfulfilled => {
      // Update category id
      this.category_id = parseInt(this.router.snapshot.paramMap.get('category_id'));

      // Calling of the question API
      this.getQuestions();

      // Setting up the category name
      this.selected_category = category;

      // Changing the next button text
      if (this.category.length === this.category_id) {
        // Submitting the assessment
        this.next = 'Submit';

      } else {
        // Submitting the assessment
        this.next = 'Next';
      }

      // Changing the next button text
      if (this.category_id <= 1 ) {
         // Hide previous button.
        this.isPreviousVisible = false;

      } else {
         // Hide previous button.
        this.isPreviousVisible = true;
      }
    });
  }

  // Next button click
  nextCategory() {
    console.log('Answered question', this.questions);

    console.error('Answered questions', this.questions);
    // calling the question save API
    this.saveQuestions();

  }

  // Previous button click
  previousCategory() {
    console.log('Answered question', this.questions);

    console.error('Answered questions', this.questions);
    // calling the question save API
    this.saveQuestions();

    if (this.category_id <= 1) {
      // calling the next category question
      const cat: CategoryInfo = this.getCategoryById(this.category_id);
      console.log('Category Info', cat);

      // Redirecting to next category
      this.onCategoryChange(cat);
    } else {
      // calling the next category question
      const cat: CategoryInfo = this.getCategoryById(this.category_id - 1);
      console.log('Category Info', cat);

      // Redirecting to next category
      this.onCategoryChange(cat);
    }
  }

  // Option select click
  optionSelected(quesion: QuestionInfo, option: questionOptionsInfo) {
    this.questions.forEach(que => {
      if (que.question_id === quesion.question_id) {
        // if question is multiple choice
        if (que.question_type !== 'single choice question') {
          que.questionOptions.forEach(op => {
            if (op.question_option_id === option.question_option_id) {
              if (op.is_selected === true) {
                op.is_selected = false;
              } else {
                op.is_selected = true;
              }
            }
          });
        } else {
          que.questionOptions.forEach(op => {
            if (op.question_option_id === option.question_option_id) {
              if (op.is_selected === true) {
                op.is_selected = false;
              } else {
                op.is_selected = true;
              }
            } else {
              if (op.is_selected === true) {
                op.is_selected = false;
              }
            }
          });
        }
      }
    });
  }

  private getCategoryById(cat_id: number): CategoryInfo {
    let selected_cat: CategoryInfo;


    this.category.forEach(cat => {
      if (cat.category_id === cat_id) {
        selected_cat = cat;
      }
    });
    return selected_cat;
  }
}
