import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '../../../node_modules/@angular/common/http';
import { catchError } from '../../../node_modules/rxjs/operators';
import { throwError } from '../../../node_modules/rxjs';
import { LoginService } from '../login/login.service';

export class CreateAssessmentInfo{
  assessment_description: string;
  assessment_id: number;
  assessment_name: string;
}

@Injectable({
  providedIn: 'root'
})
export class CreateassessmentService {

  private user_id: number = 0;
  constructor(private http: HttpClient, private login_service: LoginService) {
    this.user_id = login_service.getLoggedUser();
   }


  /**
   * Getting the assessment information using user id
   * @param assessment_id - user assessment id
   */
  createassessment (CreateAssessment : CreateAssessmentInfo){
    
    return this.http.post("/api/users/"+this.user_id+"/assessments" ,CreateAssessment )
    .pipe(
      catchError(this.handleError)
    );
  }  
    private handleError(error: HttpErrorResponse) {
      if (error.error instanceof ErrorEvent) {
        // A client-side or network error occurred. Handle it accordingly.
        console.error('An error occurred:', error.error.message);
      } else {
        // The backend returned an unsuccessful response code.
        // The response body may contain clues as to what went wrong,
        console.error(
          `Backend returned code ${error.status}, ` +
          `body was: ${error.error}`);
      }
      // return an observable with a user-facing error message
      return throwError(
        error);
    }
}
