import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {  HttpErrorResponse } from '@angular/common/http';
import { throwError } from '../../../node_modules/rxjs';
import { catchError } from '../../../node_modules/rxjs/operators';
import { LoginService } from '../login/login.service';
import { CategoryInfo } from '../questionpage/questionpage.service';

export interface ResultInfo {
  assessment_result_id: number;
  category_total_score: number;
  question_category:CategoryInfo ;
  resultMaturity: ResultMaturity;
}

export interface ResultMaturity{
  maturity_level: string;
  result_maturity_id: number ;
}

@Injectable({
  providedIn: 'root'
})
export class ResultService {
 
  user_id: number;
  
  constructor(private http:HttpClient,private login: LoginService) {
    this.user_id = this.login.getLoggedUser()
   }

  public getResult(user_id:number, assessment_id:number){
    return this.http.get<ResultInfo[]>('/api/users/'+this.user_id+'/assessments/'+assessment_id+'/result')
    .pipe(
      catchError(this.handleError)
    );
  }
  
  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      error);
  }
}
