import { Injectable } from "@angular/core";
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { LoginService } from "./login/login.service";

@Injectable()
export class CustomHttpInterceptor implements HttpInterceptor {
    constructor(private login_service: LoginService){}

    /**
     * This intersepter add the information into the header for each and every request
     * @param request - request made for the 
     * @param next - new request
     */
    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        const dummyrequest = request.clone({  
            setHeaders: {  
                'Authorization': this.login_service.getAuthToken(),
                'X-Requested-With':'XMLHttpRequest',  
                'content-type': 'application/json'  
            }  
        })  
        console.log("Cloned Request");   
        console.log(dummyrequest);   
        return next.handle(dummyrequest); 
      }

    
}
