import { Component, OnInit } from '@angular/core';
import { LoginService } from '../login/login.service';
import { UserInfo, DashboardService, AssessmentResponse } from './dashboard.service';
import { HttpErrorResponse } from '@angular/common/http';
import { RouterModule } from '@angular/router';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  // Object to bind the information
  user:UserInfo = new UserInfo();

  // AssessmentResponse array to display
  assessment_list: AssessmentResponse[];

  constructor(private login: LoginService, private dashboard_service: DashboardService) { 
    
  }

  ngOnInit() {
    console.info("Logged User : ",this.login.getLoggedUser());
    
    // Getting the user infromation using email
    this.dashboard_service.getUser(this.login.getLoggedUser()).subscribe(
      data =>{
        this.user = data;
        console.info('User Information',data);
      }, 
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null)
          console.info("Data inserted successfully");
        else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status == 404 || error.status == 504) {
            console.error("Server is not responding!!!");
          } else if (error.status == 401 || error.status == 403) {
            console.error( "Login failed");
          }
        }
      }
    );

    // Fetching the all assessments for the user
    this.dashboard_service.getAllAssessments(this.login.getLoggedUser()).subscribe(
      data =>{
        this.assessment_list = data;
        console.info('User Information',data);
      }, 
      // If any error present
      (error: HttpErrorResponse) => {
        if (error == null)
          console.info("Data inserted successfully");
        else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status == 404 || error.status == 504) {
            console.error("Server is not responding!!!");
          } else if (error.status == 401 || error.status == 403) {
            console.error( "Login failed");
          }
        }
      }
    );
  }

}
