import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { CompanySize, IndustryTpes, Contries, RegistrationService } from './registration.service';
import { RegisterUser } from './register-user';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { ToastService } from '../toast.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  
  // Variables for the hold API data
  countries: Contries[];
  company_sizes: CompanySize[];
  industry_types: IndustryTpes[];

  // Creating model
  model:RegisterUser;

  // Error message
  error_message: String;

  result:any;

  constructor(private router: Router,private reg_service: RegistrationService, private toast:ToastService) { }

  ngOnInit() {
    // Code for pulling the contries form registration service
    this.reg_service.getContries().subscribe(data =>{
      this.countries = data;
    });

    // Code for pulling the company size form registration service
    this.reg_service.getCompanySizes().subscribe(data =>{
      this.company_sizes = data;
    });

    // Code for pulling the industry types form registration service
    this.reg_service.getIndustryType().subscribe(data =>{
      this.industry_types = data;
    });
  
    // initilizastion of model
    this.model = new RegisterUser();
  }
  
  /**
   * Submit function for the registration page
   */
  onSubmit(){

    // Calling the register user API
    this.result = this.reg_service.registerUser(this.model)
      .subscribe(
        // If any data paresent
        data => {
          this.toast.Success( "Register", "User has been registered successfully");
          this.model = new RegisterUser();
          this.router.navigate(['login']);

        },
        // If any error present
        (error:HttpErrorResponse) => { 
          if (error == null)
          this.toast.Success("Register","User Registered successfully");
          else{
            // Parsing the error codes and sending the appropriate responses
            if(error.status == 404 || error.status == 504){
              this.toast.Error("Servere Issue", "Server is not responding!!!");
            }else if(error.status == 409){
              this.toast.Error("Registration", "Having truble while inserting data");
            }
          }
        }
        
      );
    
    

  }3
}