import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class PasswordService {

  constructor(private http: HttpClient, private cookie: CookieService) { }

  /**
   * API Calling for sending the password reset link to user email address.
   * @param _email user mail address
   */
  sendPasswordLink(_email: string) {
    return this.http.post('/api/forgetpassword', {email: _email})
    .pipe(
      catchError(this.handleError)
    );
  }


  /**
   * API Call for the reset password
   * @param token token generated form the user
   * @param password new password for the user
   */
  resetPassword( token: string, password: string) {
    return this.http.put('/api/forgetpassword', {token: token, password: password})
    .pipe(
      catchError(this.handleError)
    );
  }



  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      error);
  }
}
