import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { PopupComponent } from './popup/popup.component';
import { ResultComponent } from './result/result.component';
import { ForgetpasswordComponent } from './forgetpassword/forgetpassword.component';
import { ResetpasswordComponent } from './resetpassword/resetpassword.component';
import { QuestionpageComponent } from './questionpage/questionpage.component';
import { AuthguardGuard } from './login/authguard.guard';
import { SuccessfullresetComponent } from './successfullreset/successfullreset.component';
import { ForgetpasswordsuccessComponent } from './forgetpasswordsuccess/forgetpasswordsuccess.component';
import { TermsComponent } from './terms/terms.component';

const routes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthguardGuard] },
  { path: 'popup', component: PopupComponent },
  { path: 'assessment/:assessment_id/category/:category_id', component: QuestionpageComponent, canActivate: [AuthguardGuard] },
  { path: 'assessment/:assessment_id/result', component: ResultComponent, canActivate: [AuthguardGuard] },
  { path: 'resetpassword/:token', component: ResetpasswordComponent },
  { path: 'successfullreset', component: SuccessfullresetComponent },
  { path: 'forgetpasswordsuccess', component: ForgetpasswordsuccessComponent },
  { path: 'terms', component: TermsComponent },

];

@NgModule({
  imports: [
    CommonModule,
    [RouterModule.forRoot(routes)],
  ],
  declarations: [],
  exports: [RouterModule]
})
export class AppRoutingModule { }
