import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { throwError, Observable, config } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { LoginService } from '../login/login.service';
import { Config } from 'protractor';


export interface CategoryInfo {

  category_id: number;
  category_name: string;
}

export interface QuestionInfo {
  question_id: number;
  category_name: string;
  question: string;
  question_type: string;
  questionOptions: questionOptionsInfo[];
}

export interface questionOptionsInfo {
  is_selected: boolean;
  question_option: string;
  question_option_id: number;
}

@Injectable({
  providedIn: 'root'
})
export class QuestionpageService {
  // Logged user
  user_id: number;
  constructor(private http: HttpClient, private login: LoginService) {
    this.user_id = this.login.getLoggedUser();
  }

  /**
  * This method is used to get all category types form the API
  */
  getCategory() {
    return this.http.get<CategoryInfo[]>('/api/categories')
      .pipe(
        catchError(this.handleError)
      );
  }

  /**
   * This method is used to get all Questions and Options form the API
   */
  getQuestions(assessment_id: number, category_id: number) {
    return this.http.get<QuestionInfo[]>('/api/users/' + this.user_id + '/assessments/' + assessment_id + '/categories/' + category_id + '/questions')
      .pipe(
        catchError(this.handleError)
      );
  }

  /**
   * This method is created for calling the save API
   * @param assessment_id - Assessment id
   * @param category_id : category id
   * @param question : array of questions
   */
  saveQuestionAnswer(assessment_id: number, category_id: number, question: QuestionInfo[]){
    return this.http.put<QuestionInfo[]>('/api/users/' + this.user_id + '/assessments/' + assessment_id + '/categories/' + category_id + '/questions', question)
    .pipe(
      catchError(this.handleError)
    );
  }

  /**
   * This method is created for submit the assessment
   * @param assessment_id : Assessment id for submit the API
   */
  submitAssessment(assessment_id: number): Observable<HttpResponse<Config>>{
    return this.http.get<Config>('/api/users/' + this.user_id + '/assessments/' + assessment_id + '/submit', { observe: 'response' })
    .pipe(
      catchError(this.handleError)
    );
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      error);
  }
}
