/**
 * Title : registration service
 * Description: This service created for perform an registration operation
 */

import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { HttpErrorHandlerService, HandleError } from '../http-error-handler.service';
import { RegisterUser } from './register-user';
import { Resolve } from '@angular/router';
import { ResolvedUser } from './resolved-user';
import { HttpResponse } from 'selenium-webdriver/http';


const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'application/json',
  })
};

// Interface to hold the number of contries
export interface Contries { 
  country_id: number; 
  country_name: string 
}

// Interface for hold the all industry sizes
export interface IndustryTpes{
  industry_type_id: number;
  industry_name: string;
}

// Interface for hold the company sizes
export interface CompanySize {
  company_size_id: number;
  size: string;
}

@Injectable({
  providedIn: 'root'
})
export class RegistrationService{

    constructor(private http: HttpClient) { 
  }

  /**
   * This method call the all contries form the database.
   */
  getContries() {
    return this.http.get<Contries[]>('/api/countries');
  }

  /**
   * This method is used to get all industry types form the API
   */
  getIndustryType(){
    return this.http.get<IndustryTpes[]>('/api/industries');
  }

  /**
   * This method is used to get all company sizes from API
   */
  getCompanySizes(){
    return this.http.get<CompanySize[]>('/api/companysize');
  }

  //////// Save methods //////////

  /** POST: add a new hero to the database */
  registerUser (user: RegisterUser) {
    return this.http.post("/api/register", user, httpOptions)
      .pipe(
        catchError(this.handleError)
      );
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      error);
  }
}
