import { Component, OnInit } from '@angular/core';
import { LoginService } from '../login/login.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { CreateassessmentService, CreateAssessmentInfo } from './createassessment.service';
import { ToastService } from '../toast.service';

@Component({
  selector: 'app-header1',
  templateUrl: './header1.component.html',
  styleUrls: ['./header1.component.css']
})
export class Header1Component implements OnInit {

  constructor(private login: LoginService , private create_service: CreateassessmentService, private router: Router, private cookies:CookieService, private toast:ToastService, private route:ActivatedRoute) { }
  private logged_out:Boolean = true;

  // Created model post data
  model:CreateAssessmentInfo;

   // Error message
   error_message: String;

  result : any;

  // Activated path
  active_path: string;

  ngOnInit() {
    this.model=new CreateAssessmentInfo();
    /*setting up the assessment Id*/
    this.model.assessment_id=0;

    // Setting up the dynamic button
    this.active_path = this.route.snapshot.url[0].path;
  }
  /**
   * create function for create assessment 
   */
  onSubmit(){

      this.result = this.create_service.createassessment(this.model)
      .subscribe(
        // If any data paresent
        data => {
          this.toast.Success("Create Assessment", "User has created assessment successfully");
          
        },
        // If any error present
        (error:HttpErrorResponse) => { 
          if (error == null)
          this.toast.Success("Create Assessment", "Assessment Created successfully");
          else{
            // Parsing the error codes and sending the appropriate responses
            if(error.status == 404 || error.status == 504){
              this.toast.Error("Server Issue", "Server is not responding!!!");
            }else if(error.status == 409){
              this.toast.Error("Create Assessment","Having truble while inserting data");
            }
          }
        }
        
      );
  }
  logout() {
    this.login.logoutUser().subscribe(
      data => {
        this.toast.Success("Logout","Successfully logged out.");
      },
      (error: HttpErrorResponse) => {
        this.logged_out = false;
        if (error == null)
          console.info("Data inserted successfully");
        else {
          // Parsing the error codes and sending the appropriate responses
          if (error.status == 404 || error.status == 504) {
            console.error("Server is not responding!!!");
          } else if (error.status == 401 || error.status == 403) {
            console.error("Login failed");
          }
        }
      }
    );
    if(this.logged_out) {
        this.login.setUserLoggedIn(false);
        this.cookies.deleteAll();
        this.router.navigateByUrl('/login');
      }

  }
}
