
import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ResultService } from './result.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Chart } from 'chart.js';
import html2canvas from 'html2canvas';
import * as jsPDF from 'jspdf';

@Component({
  selector: 'app-result',
  templateUrl: './result.component.html',
  styleUrls: ['./result.component.css']
})
export class ResultComponent implements OnInit {

  chart = [];
  chart2 = [];
  user_id: number;

  assessment_id: number;

  constructor(private resultService: ResultService, private router: ActivatedRoute) {
    this.assessment_id = parseInt(this.router.snapshot.paramMap.get('assessment_id'));
  }

  ngOnInit() {

    this.resultService.getResult(this.user_id, this.assessment_id)
      .subscribe(data => {
        let cat_names = data.map(res => res.question_category.category_name);
        let cat_scores = data.map(res => res.category_total_score);
        let cat_maturities = data.map(res => res.resultMaturity.maturity_level);
        console.info('Result infromation', cat_names, cat_scores, cat_maturities);


        this.chart2 = new Chart('canvas2', {
          type: 'radar',
          data: {
            labels: ['Build', 'Deployment', 'Infrastructure', 'Monitoring', 'Process', 'Technology'],
            datasets: [
              {

                borderColor: '#3cba9f',
                fill: false,
                data: cat_scores,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)'
                ],

              },

            ]
          },
          options: {
            legend: {
              display: false
            },

            scales: {
              xAxes: [
                {

                  display: false
                }
              ],
              yAxes: [

                {
                  display: false
                }
              ]
            }
          }
        });
        this.chart = new Chart('canvas1', {
          type: 'bar',
          data: {
            labels: ['Build', 'Deployment', 'Infrastructure', 'Monitoring', 'Process', 'Technology'],
            datasets: [
              {
                borderColor: '#3cba9f',
                fill: true,
                data: cat_scores,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)'
                ],

              },

            ]
          },
          options: {
            legend: {
              display: false
            },

            scales: {
              xAxes: [
                {
                  scaleLabel: {
                    display: true,
                    labelString: 'Category'
                  },
                  gridLines: {
                    display: false
                  }
                }],
              yAxes: [
                {
                  ticks: {
                    max: 5,
                    min: 0,
                    stepSize: 1
                  },

                  scaleLabel: {
                    display: true,

                    labelString: 'Maturity'
                  }
                }]
            }
          }
        });
      });
  }

  public downloadPDF() {
    var data = document.getElementById('report');
    html2canvas(data).then(canvas => {
      // Few necessary setting options.  
      var imgWidth = 208;
      var pageHeight = 150;
      var pageWidth = 150;
      var imgHeight = canvas.height * imgWidth / canvas.width;
      var heightLeft = imgHeight;

      const contentDataURL = canvas.toDataURL('image/png')
      let pdf = new jsPDF('p', 'mm', 'a4'); // A4 size page of PDF  
      var position = 0;
      pdf.addImage(contentDataURL, 'PNG', 0, position, imgWidth, imgHeight)
      pdf.save('report.pdf'); // Generated PDF.
    });
  }


}



