import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError } from 'rxjs';
import { CATCH_ERROR_VAR } from '@angular/compiler/src/output/output_ast';
import { catchError } from 'rxjs/operators';

export class UserInfo {
  user_id: number;
  user_email: string;
  first_name: string;
  last_name: string;
  companyName: string;
  companySize: string;
  country: string;
  industryType: string;
  links: any;
}

// Interface for the assessment response
export interface AssessmentResponse {
  assessmentStatus: string;
  assessment_description: string;
  assessment_id: number;
  assessment_name: string;
  created_date: Date;
}

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(private http: HttpClient) { }

  /**
   * Getting the user information using email address
   * @param email - user id
   */
  public getUser(user_id: number) {
    return this.http.get<UserInfo>("/api/users/" + user_id)
      .pipe(
        catchError(this.handleError)
      );
  }

  /**
   * Get all assessment for a perticular user
   * @param user_id - user unique id
   */
  public getAllAssessments(user_id: number){
    return this.http.get<AssessmentResponse[]>("/api/users/"+user_id+"/assessments")
      .pipe(
        catchError(this.handleError)
      );
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      error);
  }
}
